*{
    margin: 0;
    padding: 0;
   /* border: 1px solid red; */

}

nav{
    height: 3vh;
    background-color: aqua;
    font-family: sans-serif;
    text-align: center;
    font-size: 1.4rem;
    padding: 10px;
}

.container{
    width: 80vw;
    background-color: antiquewhite;
    margin: auto;
    min-height: 95vh;
    box-sizing: border-box;
    padding: 4vmax 8vmax;
}
.container > form{
    display: flex;
    flex-direction: column;
    
}
.container  > h1{
    text-align: center;
    margin-bottom: 30px;
}

.container > form >input,textarea,button{
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 20px;
    border-radius: 5px;
    font-size: 1.4rem;
}

.container > form >button{
    background-color: rgba(0, 0, 0, 0.889);
    color: white;
    cursor: pointer;
    border-radius: none;
}

.task{
   display: flex;
   padding: 10px;
   background-color: white;

}
.task > div{
    width: 100%;
}
.task > div > p{
    font-size: 1.8rem;
    font-weight: 500;
}
.task > div > span{
    font-size: 1.2rem;
    font-weight: 300;
    color: rgba(0, 0, 0, 0.407);
}
.task >button{
    background-color: rgba(0, 0, 0, 0.889);
    color: white;
    cursor: pointer;
    border-radius: none;
    border-radius: 100%;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.6rem;
}

.task >button:hover{
    background-color: crimson;
}